.info{
    background-color:#1A1B21;
}
.info-detail{
    text-align: center;
}
.info-detail p:nth-child(1){
    font-size: 25px;
    font-weight: 700;
    margin:20px 0 0 0;
    color: #fff;
}
.info-detail p:nth-child(2){
    font-size: 12.8px;
    font-weight: 400;
    margin: 6px 0 0 0;
    color: #F3BF99;
}
.info-detail p:nth-child(3){
    font-size: 10.24px;
    font-weight: 400;
    margin: 8px 0 0 0 ;
    color: #F5F5F5;
}
.button{
    display: flex;
    justify-content: space-evenly;
}
.button p:nth-child(1){
    font-size: 14px;
    display: flex;
    gap: 8px;
    align-items: center;
    background-color: #fff;
    width: 100px;
    height: 30px;
    justify-content: center;
    border-radius: 4px;

    
}
.button p:nth-child(2){
    font-size: 14px;
